.panel-wrap {
  height: 100vh;
  width: 100vw;
}
h1 {
  font-size: 20vmin;
  margin: 0;
}
.panel-wrap {
  -webkit-perspective: 2000px;
          perspective: 2000px;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}
.panel-wrap.animate--shrink,
.panel-wrap.animate--tilt,
.panel-wrap.animate--tilt2 {
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s;
  transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s;
  -o-transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s;
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s;
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s, -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1) 0.3s;
}

.panel {
  background-color: transparent;
  height: 100vh;
  position: absolute;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  width: 100vw;
  will-change: transform;
  /* padding-bottom: -25px; */
  /* background-image: url(../assets/background.jpeg);
  background-size: cover; */
}
.panel:before {
  /* background-color: transparent; */
  bottom: 0;
  content: "";
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transition: background-color 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: background-color 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  transition: background-color 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: 2;
}
.show-all .panel:before {
  pointer-events: all;
}
.show-all .panel:hover:before {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
.animate--shrink.animate .panel {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}
.animate--tilt.animate .panel {
  -webkit-transform: scale(0.6) rotateY(-25deg);
          transform: scale(0.6) rotateY(-25deg);
}
.animate--tilt2.animate .panel {
  -webkit-transform: scale(0.8) rotateX(25deg);
          transform: scale(0.8) rotateX(25deg);
}
.panel h1 {
  cursor: default;
  left: 50%;
  line-height: 1;
  position: absolute;
  text-align: center;
  /* fixes clock position */
  top: 42%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.panel[data-x-pos="-2"] {
  left: -200%;
}
.panel[data-x-pos="-1"] {
  left: -100%;
}
.panel[data-x-pos="1"] {
  left: 100%;
}
.panel[data-x-pos="2"] {
  left: 200%;
}
.panel[data-y-pos="-2"] {
  top: 200%;
}
.panel[data-y-pos="-1"] {
  top: 100%;
}
.panel[data-y-pos="1"] {
  top: -100%;
}
.panel[data-y-pos="2"] {
  top: -200%;
}
.panel__zoom {
  cursor: pointer;
  left: 50%;
  opacity: 1;
  position: absolute;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%) translateY(-12vmin);
      -ms-transform: translateX(-50%) translateY(-50%) translateY(-12vmin);
          transform: translateX(-50%) translateY(-50%) translateY(-12vmin);
  -webkit-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 10;
}
.panel__zoom:hover {
  opacity: 1;
}
.show-all .panel__zoom {
  pointer-events: none;
}
.panel__nav {
  cursor: pointer;
  opacity: .9;
  position: absolute;
  -webkit-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  will-change: opacity;
}
.panel__nav--up {
  left: 50%;
  top: 1rem;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.panel__nav--left {
  left: 1rem;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(-90deg) translateX(-100%);
      -ms-transform: translateY(-50%) rotate(-90deg) translateX(-100%);
          transform: translateY(-50%) rotate(-90deg) translateX(-100%);
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
}
.panel__nav--left-top {
  left: 1rem;
  top: 1rem;
}
.panel__nav--left-down {
  bottom: 1rem;
  left: 1rem;
}
.panel__nav--right {
  right: 1rem;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(90deg) translateX(100%);
      -ms-transform: translateY(-50%) rotate(90deg) translateX(100%);
          transform: translateY(-50%) rotate(90deg) translateX(100%);
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
}
.panel__nav--right-top {
  right: 1rem;
  top: 1rem;
}
.panel__nav--right-down {
  bottom: 1rem;
  right: 1rem;
}
.panel__nav--down {
  bottom: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.panel__nav:hover {
  opacity: 1;
}
.panel__animation-list {
  font-size: 3.3vmin;
  left: 50%;
  position: absolute;
  top: 45%;
  -webkit-transform: translateX(-50%) translateY(-50%) translateY(14vmin);
      -ms-transform: translateX(-50%) translateY(-50%) translateY(14vmin);
          transform: translateX(-50%) translateY(-50%) translateY(14vmin);
}
.panel__animation-list span {
  cursor: pointer;
  display: inline-block;
  opacity: .2;
  position: relative;
  -webkit-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  transition: opacity 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.panel__animation-list span:after {
  border-bottom: .3vmin solid transparent;
  bottom: 0;
  content: "";
  left: -1px;
  position: absolute;
  right: 0;
  -webkit-transition: border-color 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  -o-transition: border-color 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  transition: border-color 0.9s cubic-bezier(0.55, 0, 0.1, 1);
  width: 100%;
}
.panel__animation-list span.active,
.panel__animation-list span:hover {
  opacity: 1;
}
.panel__animation-list span.active:after,
.panel__animation-list span:hover:after {
  border-color: white;
}

